<template>
<div>
  
<van-form @submit="onSubmitSupportInfo">

    <van-collapse v-model="activeMore">
      <van-collapse-item title="基本信息" name="basicParam"   title-class="collapseTitle">
        <van-row class="myborder">
            <van-col span="24">  
                <van-field v-model="MiaoMuName"   label="苗木名"   right-icon="warning-o" placeholder="输入苗木名"/>
                
            </van-col>
        </van-row>
         <van-row class="myborder">
            <van-col span="24">  
                <van-field readonly clickable name="picker" :value="MiaoMuType"   label="苗木类目"     placeholder="选择苗木类目" @click="showleimuPicker = true"/>
            <van-popup v-model="showleimuPicker" position="bottom">
                    <van-picker show-toolbar :columns="LMcolumns"  @confirm="onLMConfirm"  @cancel="showleimuPicker = false" />
                </van-popup>
            </van-col>
        </van-row>
        <van-row class="myborder"> 
            <van-col span="14">  
                <van-field readonly clickable name="picker" :value="JGvalue" label="杆径" placeholder="点击选择径杆" @click="showPicker = true"/>
                <van-popup v-model="showPicker" position="bottom">
                    <van-picker show-toolbar :columns="JGcolumns"  @confirm="onJGConfirm"  @cancel="showPicker = false" />
                </van-popup>
            </van-col >
              <van-col span="6">
                <van-field v-model="GJgongfen"        placeholder="输入公分"/>
            </van-col>
             <van-col span="4">
                 <van-cell  value="公分"/>
            </van-col>
        </van-row>
         <van-row class="myborder">
            <van-col span="10">  
                <van-field v-model="MiaoMuGaoduL"   label="高度"    placeholder="最小"/>
            </van-col>
            <van-col span="4">  
                <van-cell   style="padding-left:5px;padding-right:5px;" value="公分-"/>
            </van-col>
            <van-col span="6">  
                <van-field v-model="MiaoMuGaoduH"    type="number"  placeholder="最大"/>
            </van-col>
            <van-col span="4">  
                <van-cell   value="公分"/>
            </van-col>
        </van-row>  
         <van-row class="myborder">
            <van-col span="10">  
                <van-field v-model="MiaoMuGuanfuL"   label="冠幅" type="number"   placeholder="最窄"/>
            </van-col>
            <van-col span="4">  
                <van-cell  style="padding-left:5px;padding-right:5px;" value="公分-"/>
            </van-col>
            <van-col span="6">  
                <van-field v-model="MiaoMuGuanfuH" type="number"    placeholder="最宽"/>
            </van-col>
            <van-col span="4">  
                <van-cell   value="公分"/>
            </van-col>
        </van-row>  
         <van-row class="myborder">
            <van-col span="24">  
                <van-field readonly clickable name="picker" :value="MiaoMuStatus"   label="种植状态"     placeholder="苗木种植状态" @click="showZZZTPicker = true"/>
            <van-popup v-model="showZZZTPicker" position="bottom">
                    <van-picker show-toolbar :columns="ZZZTcolumns"  @confirm="onZZZTConfirm"  @cancel="showZZZTPicker = false" />
                </van-popup>
            </van-col>
        </van-row>
          <van-row class="myborder">
            <van-col span="10">  
                <van-field v-model="MiaoMuPrice"   label="价格"  type="number"  placeholder="元"/>
            </van-col>
            <van-col span="4">  
                <van-cell  style="padding-left:5px;padding-right:5px;" v-bind:value="MiaoMuUnit"/>
            </van-col>
            <van-col span="10">  
                <van-field v-model="MiaoMuNum"   label="数量"   type="number" v-bind:placeholder="MiaoMuUnit"/>
            </van-col>            
        </van-row> 
        <van-row class="myborder">
            <van-col span='24'>
                <van-field v-model="MiaoMuAddress" rows="2" autosize label="供货地址" type="textarea"  placeholder="供货地址" show-word-limit/> 
            </van-col>
        </van-row>
       <van-row class="myborder">
           <van-col span="6">  
                <van-cell  style="padding-left:5px;padding-right:5px;" value="热门推荐"/>
            </van-col>
             <van-col span="4">  
                 <van-cell-group>
                    <van-switch v-model="IsRemenTuijian" size="30px" style="margin: 7px 1px 7px 1px" />
                </van-cell-group>      
            </van-col>
            <van-col span="14" v-show="IsRemenTuijian">  
                <van-field readonly clickable name="picker" :value="TSvalue"  label="热门天数"  type="number"  placeholder="选择推荐天数" @click="showTisnshuPicker = true"/>
                   <van-popup v-model="showTisnshuPicker" position="bottom">
                    <van-picker show-toolbar :columns="TScolumns"  @confirm="onTSConfirm"  @cancel="showTisnshuPicker = false" />
                </van-popup>
            </van-col>
        </van-row> 
        
       
        <van-row class="myborder" style="background-color:rgb(247, 248, 249);">
            <h5 style="margin:5px 5px"><span>添加图片      </span><span style="color:red;">最多上传9张图片！</span></h5> 
                <van-col span='24'>
            <!-- <van-uploader :after-read="afterRead"  :before-read="beforeRead" v-model="fileList" :max-count="9" multiple/> -->
             <van-uploader :after-read="afterRead" :before-read="beforeRead" v-model="fileList" :max-count="9" multiple/>
            </van-col>
        </van-row>
    
     </van-collapse-item>
    </van-collapse>

    <van-collapse v-model="activeMore">
    <van-collapse-item title="更多信息" name="moreParam"   title-class="collapseTitle">
     <van-row class="myborder">
     <van-col span="6">
           <van-cell  value="树冠"/>
     </van-col>
     <van-col span="6">
          <van-dropdown-menu>
           <van-dropdown-item v-model="valueSG" :options="optionSG" />
        </van-dropdown-menu>
        </van-col>
       <van-col span="6">
             <van-cell  value="品质"/>
       </van-col>
      <van-col span="6">
          <van-dropdown-menu>
           <van-dropdown-item v-model="valuePZ" :options="optionPZ" />
        </van-dropdown-menu>
      </van-col>
            
        </van-row>
        <van-row class="myborder">
     <van-col span="6">
           <van-cell  value="土质"/>
     </van-col>
     <van-col span="6">
          <van-dropdown-menu>
           <van-dropdown-item v-model="valueTZ" :options="optionTZ" />
        </van-dropdown-menu>
        </van-col>
       <van-col span="6">
             <van-cell  value="树型"/>
       </van-col>
      <van-col span="6">
          <van-dropdown-menu>
           <van-dropdown-item v-model="valueSX" :options="optionSX" />
        </van-dropdown-menu>
      </van-col>
            
        </van-row>
           <van-row class="myborder">
     <van-col span="6">
           <van-cell  value="茂密度"/>
     </van-col>
     <van-col span="6">
          <van-dropdown-menu>
           <van-dropdown-item v-model="valueMMD" :options="optionMMD" />
        </van-dropdown-menu>
        </van-col>
       <van-col span="6">
             <van-cell  value="苗木形态"/>
       </van-col>
      <van-col span="6">
          <van-dropdown-menu>
           <van-dropdown-item v-model="valueXT" :options="optionXT" />
        </van-dropdown-menu>
      </van-col>
            
        </van-row>
        <van-row class="myborder">
            <van-col span="10">  
                <van-field v-model="MiaoMuGaoduL"   label="分枝点"    placeholder="最小"/>
            </van-col>
            <van-col span="4">  
                <van-cell   style="padding-left:5px;padding-right:5px;" value="公分-"/>
            </van-col>
            <van-col span="6">  
                <van-field v-model="MiaoMuGaoduH"    type="number"  placeholder="最大"/>
            </van-col>
            <van-col span="4">  
                <van-cell   value="公分"/>
            </van-col>
        </van-row>  
         <van-row class="myborder">
              <van-col span="6">  
                <van-cell   value="报价方式"/>
            </van-col>
            <van-col span="18">
            <van-radio-group v-model="MiaoMuBaoJia" direction="horizontal">
            <van-radio name="1" icon-size="24px"  style="margin: 11px 1px 11px 10px" >上车价</van-radio>
            <van-radio name="2" icon-size="24px"  style="margin: 11px 1px 11px 10px" >到货价</van-radio>
            </van-radio-group>
            </van-col>
          
        </van-row>
          <van-row class="myborder">
               
          <van-field
            readonly
            clickable
            name="datetimePicker"
            :value="DeadDate"
            label="时间选择"
            placeholder="点击选择时间"
            @click="showDeadTimePicker = true"
            />
        <van-popup v-model="showDeadTimePicker" position="bottom">
        <van-datetime-picker
            type="datetime"
            :min-date="minDate"
            :max-date="maxDate"
            @confirm="onDeadTimeConfirm"
            @cancel="showDeadTimePicker = false"
        />
        </van-popup>
           
             </van-row>
      </van-collapse-item>
  
</van-collapse>
    <div style="margin: 20px 20px 60px 20px;"> 
        <van-button round block type="primary" native-type="submit">
        确认发布供应
        </van-button>
  </div>
    </van-form>
    
</div>
</template>
<style  >
.collapseTitle{
    font-size: 20px;
    font-weight: 700;
    color:#1989fa;
}
.van-collapse-item__content
{
    padding: 5px;
    color: #969799;
    font-size: 13px;
    line-height: 1.5;
    background-color: #fff;
}
.myborder{
    border-bottom: solid 1px #e8d8d8;
}
 
</style>
<script>
import Vue from 'vue';
 
import {Button,Form, Field,Col, Row ,Popup,Picker,Switch,Cell, CellGroup ,Uploader,Toast,Panel,Collapse, CollapseItem ,DropdownMenu, DropdownItem, DatetimePicker,RadioGroup, Radio } from 'vant';


Vue.use(DatetimePicker);
Vue.use(Radio);
Vue.use(RadioGroup);
Vue.use(DropdownMenu);
Vue.use(DropdownItem);
Vue.use(Collapse);
Vue.use(CollapseItem);
Vue.use(Panel);
Vue.use(Toast);
Vue.use(Uploader);
Vue.use(Cell);
Vue.use(CellGroup);
Vue.use(Switch);
Vue.use(Picker);
Vue.use(Popup);
Vue.use(Col);
Vue.use(Row);
Vue.use(Field);
Vue.use(Form);
Vue.use(Button);
export default {
  data() {
    return {
        testcount:1,
        MiaoMuName:'',
        GJgongfen:'',
        MiaoMuGaoduL:'',
        MiaoMuGaoduH:'',
        JGvalue: '',
        TSvalue:'',
        valueSG:0,
        valuePZ:0,
        valueTZ:0,
        valueSX:0,
        valueMMD:0,
        valueXT:0,
        MiaoMuType:'',
        MiaoMuStatus:'',
        MiaoMuBaoJia:'1',
        MiaoMuNum:'',
        MiaoMuGuanfuH:'',
        MiaoMuGuanfuL:'',
        MiaoMuPrice:'',
        MiaoMuAddress:'',
        MiaoMuUnit:'/株',
        DeadDate:'',
        showDeadTimePicker:false,
        IsRemenTuijian:true,
        showPicker: false,
        showTisnshuPicker:false,
        showleimuPicker:false,
        showZZZTPicker:false,
        minDate: new Date(2020, 0, 1),
        maxDate: new Date(2026, 0, 1),
        DeadTime: '2020-03-03',
        JGcolumns: ['杭州', '宁波', '温州', '嘉兴', '湖州'],
        TScolumns: ['1', '3', '5', '7'],
        LMcolumns: ['乔木', '种子', '花苗', '水生','蕨类','灌木'],
        ZZZTcolumns: ['原生苗', '断根苗', '移栽苗', '容器苗'],
        activeMore: ['basicParam'],
        fileList: [],
       optionSG: [
        { text: '请选择', value: 0 },
        { text: '全冠', value: 1 },
        { text: '半冠', value: 2 },
        { text: '骨架', value: 3 },
        { text: '截杆', value: 4 }
      ],
      optionPZ: [
        { text: '请选择', value: 0 },
        { text: 'A-精品', value: 1 },
        { text: 'B-中等', value: 2 },
         { text: 'C-一般', value: 3 },
          { text: 'D-较差', value: 4 }
      ],
       optionTZ: [
        { text: '请选择', value: 0 },
        { text: '黄土', value: 1 },
        { text: '沙土', value: 2 },
         { text: '红土', value: 3 },
          { text: '黑土', value: 4 }
      ],
       optionSX: [
        { text: '请选择', value: 0 },
        { text: '伞型', value: 1 },
        { text: '塔型', value: 2 },
         { text: '卵型', value: 3 },
          { text: '柱型', value: 4 },
           { text: '卵型', value: 5 },
          { text: '球型', value: 6 },
        { text: '自然型', value: 7 }        
      ],
      optionMMD: [
        { text: '请选择', value: 0 },
        { text: '饱满', value: 1 },
        { text: '稀疏', value: 2 }
      ],
       optionXT: [
        { text: '请选择', value: 0 },
        { text: '单杆', value: 1 },
        { text: '多杆', value: 2 },
        { text: '丛生', value: 3 }
      ],
    };
  },
  mounted(){
console.log(this.fileList);
  },
  methods: {
    onSubmitSupportInfo(values) {
      console.log('submit', values);
    },
    onJGConfirm(value) {
      this.JGvalue = value;
      this.showPicker = false;
    },
    onTSConfirm(value) {
      this.TSvalue = value;
      this.showTisnshuPicker = false;
    },
    onLMConfirm(value) {
      this.MiaoMuType = value;
      this.showleimuPicker = false;
    },
    onZZZTConfirm(value) {
      this.MiaoMuStatus = value;
      this.showZZZTPicker = false;
    },
    onDeadTimeConfirm(tdate){
      this.showDeadTimePicker = false;
      // date.getFullYear();
      console.log(tdate);
      this.DeadDate =   this.formatDate(tdate);
     // console.log( this.formatDate(date));
     },
      formatDate(val) {
      let year = val.getFullYear()
      let month = val.getMonth() + 1
      let day = val.getDate()
      let hour = val.getHours()
      let minute = val.getMinutes()
      if (month >= 1 && month <= 9) { month = `0${month}` }
      if (day >= 1 && day <= 9) { day = `0${day}` }
      if (hour >= 0 && hour <= 9) { hour = `0${hour}` }
      if (minute >= 0 && minute <= 9) { minute = `0${minute}` }
       
      return `${year}-${month}-${day} ${hour}:${minute}`
       
    },
     afterRead(file,detail) {
      // 此时可以自行将文件上传至服务器
     // console.log(this.fileList);
     // console.log('after'+this.testcount );
     // var obj = {url: './static/mock/img/pd'+this.testcount+'.jpg'}
     // this.fileList.push(obj);
     // console.log(this.fileList.length);

    }, 
    beforeRead(file,detail) {
        console.log(file);
        console.log(detail);
     if(file.length==undefined)
     {
        if (file.type !== 'image/jpeg') {
        Toast('请上传 jpg 格式图片');
        return false;
      }
 
     }
     else
     {
         for(var i = 0;i<file.length;i++)
         {
        if (file[i].type !== 'image/jpeg') {
            Toast('请上传 jpg 格式图片');
            return false;
        }
 
         }
     }
     // console.log('before'+ this.testcount);
      return true;
    },
 
  },
};
</script>


